.header {
  height: 100px;
  box-shadow: 0 -10px 25px #000;
  display: flex;
  align-items: center; }
  .header__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-transform: uppercase; }
  .header__wrap .button {
    display: none;
    flex-direction: column;
    width: 100%;
    height: 200px;
    padding: 80px 20px;
    background: #eff4f4;
    box-shadow: 0 1px 20px #000;
    position: absolute;
    top: 0;
    left: 0; }
  .header__wrap .button--active {
    display: flex; }
  .header__wrap img {
    height: 28px;
    margin-left: 180px;
    z-index: 100; }
  .header__btn {
    position: relative;
    width: 40px;
    height: 40px;
    border: none;
    background-color: transparent;
    cursor: pointer;
    z-index: 100; }
  .header__btn span,
  .header__btn span:before,
  .header__btn span:after {
    position: absolute;
    top: calc(50% - 1px);
    left: calc(50% - 10px);
    width: 20px;
    height: 2px;
    background-color: #2cd3b8;
    transition: all 0.4s; }
  .header__btn span:before {
    content: '';
    top: 5px; }
  .header__btn span:after {
    content: '';
    top: -5px; }
  .header__btn--active span {
    background-color: transparent; }
  .header__btn--active span:before {
    top: 0;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg); }
  .header__btn--active span:after {
    top: 0;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg); }

