.app {
	&__wrap {
		padding-top: 20px;
		padding-bottom: 40px; }
	&__wrap h3 {
		color: #393e6b;
		text-transform: uppercase;
		width: 80%; }
	&__wrap p {
		font-size: 12px;
		color: #74757b; }
	&__wrap ul {
		margin-top: 30px;
		padding-left: 20px; }
	&__wrap ul li {
		color: #393e6b; }
	&__wrap ul li a {
		display: block;
		width: 80%;
		font-size: 14px;
		font-weight: 600;
		color: #2cd3b8; }
	&__wrap ul li a:hover {
		color: #393e6b; }
	&__wrap ul li p {
		font-size: 12px;
		color: #74757b; } }

@media (min-width: 900px) {
	.app {
		&__wrap {
			padding-top: 40px;
			padding-bottom: 60px; }
		&__wrap h3 {
			font-size: 30px; }
		&__wrap p {
			font-size: 16px; }
		&__wrap ul {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			height: 490px;
			margin-top: 60px;
			width: auto; }
		&__wrap ul li a {
			font-size: 24px; }
		&__wrap ul li p {
			font-size: 16px; } } }
