.header {
	height: 100px;
	box-shadow: 0 -10px 25px #000;
	display: flex;
	align-items: center;
	&__wrap {
		display: flex;
		justify-content: space-between;
		align-items: center;
		text-transform: uppercase; }
	&__wrap .button {
		display: none;
		flex-direction: column;
		width: 100%;
		height: 200px;
		padding: 80px 20px;
		background: #eff4f4;
		box-shadow: 0 1px 20px #000;
		position: absolute;
		top: 0;
		left: 0; }
	&__wrap .button--active {
		display: flex; }
	&__wrap img {
		height: 28px;
		margin-left: 180px;
		z-index: 100; }
	&__btn {
		position: relative;
		width: 40px;
		height: 40px;
		border: none;
		background-color: transparent;
		cursor: pointer;
		z-index: 100; }
	&__btn span,
	&__btn span:before,
	&__btn span:after {
		position: absolute;
		top: calc(50% - 1px);
		left: calc(50% - 10px);
		width: 20px;
		height: 2px;
		background-color: #2cd3b8;
		transition: all 0.4s; }
	&__btn span:before {
		content: '';
		top: 5px; }
	&__btn span:after {
		content: '';
		top: -5px; }
	&__btn--active span {
		background-color: transparent; }
	&__btn--active span:before {
		top: 0;
		transform: rotate(45deg); }
	&__btn--active span:after {
		top: 0;
		transform: rotate(-45deg); } }


.user-nav {
	display: flex;
	flex-direction: column;
	& a {
		color: #393e6b;
		text-decoration: none; }
	& a:hover {
		color: #2cd3b8; } }

.nav {
	& ul {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 120px;
		margin-top: 50px;
		list-style: none;
		padding: 0; }
	& ul li a {
		color: #393e6b;
		margin-right: 20px;
		text-decoration: none; }
	& ul li a:hover {
		color: #2cd3b8; } }

@media (min-width: 900px) {
	.header {
		&__wrap {
			width: 90%; }
		&__wrap .button {
			display: flex;
			flex-direction: row;
			align-items: center;
			width: 100%;
			height: auto;
			padding: 0;
			background: transparent;
			box-shadow: none;
			position: relative;
			top: 0;
			left: 0; }
		&__btn {
			display: none; } }

	.user-nav {
		display: flex;
		align-items: center;
		flex-direction: row;
		& a {
			color: #393e6b;
			text-decoration: none;
			margin-right: 20px; } }

	.nav {
		display: flex;
		align-items: center;
		margin-left: auto;
		& ul {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			height: 2px;
			margin-top: 0; } } }
