.option {
	&__wrap {
		padding-top: 20px;
		padding-bottom: 20px; }
	&__wrap p {
		font-size: 12px;
		color: #393e6b; }
	&__wrap ul {
		list-style: none;
		padding: 0;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		min-height: 250px;
		margin-top: 30px; }
	&__wrap ul li {
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		padding-left: 40px; }
	&__wrap ul li::before {
		content: '';
		position: absolute;
		top: calc(50% - 15px);
		left: 0;
		width: 30px;
		height: 30px;
		background: url(../img/sn.svg); }
	&__wrap ul li:nth-child(2)::before {
		background: url(../img/msn.svg); }
	&__wrap ul li:nth-child(3)::before {
		background: url(../img/chatbots.svg); }
	&__wrap ul li:nth-child(4)::before {
		background: url(../img/feedback.svg); }
	&__wrap ul li:nth-child(5)::before {
		background: url(../img/yad.svg); }
	&__wrap ul li:nth-child(6)::before {
		background: url(../img/app.svg); }
	&__wrap ul li:nth-child(7)::before {
		background: url(../img/email.svg); }
	&__wrap ul li a {
		color: #2cd3b8;
		font-size: 12px; }
	&__wrap ul li a:hover {
		color: #393e6b; } }

@media (min-width: 900px) {
	.option {
		&__wrap {
			padding-top: 60px;
			padding-bottom: 60px; }
		&__wrap p {
			font-size: 16px;
			width: 60%; }
		&__wrap ul {
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: center;
			height: 350px; }
		&__wrap ul li {
			padding-left: 70px;
			width: 270px; }
		&__wrap ul li::before {
			top: calc(50% - 20px);
			left: 0;
			width: 40px;
			height: 40px; }
		&__wrap ul li a {
			font-size: 18px; } } }
