.step {
	background-color: #eff4f4;
	&__wrap {
		padding-top: 20px;
		padding-bottom: 40px; }
	&__wrap h3 {
		color: #393e6b;
		text-transform: uppercase;
		width: 80%; }
	&__wrap p {
		font-size: 12px;
		color: #74757b; }
	&__wrap ul {
		list-style: none;
		padding: 0;
		display: flex;
		flex-direction: column;
		justify-content: space-between; }
	&__wrap ul li {
		position: relative;
		padding-left: 40px; }
	&__wrap ul li::before {
		content: '';
		position: absolute;
		top: calc(50% - 15px);
		left: 0;
		width: 30px;
		height: 30px;
		background: url(../img/step_01.svg); }
	&__wrap ul li:nth-child(2)::before {
		background: url(../img/step_02.svg); }
	&__wrap ul li:nth-child(3)::before {
		background: url(../img/step_03.svg); }
	&__wrap ul li:nth-child(4)::before {
		background: url(../img/step_04.svg); }
	&__wrap ul li:nth-child(5)::before {
		background: url(../img/step_05.svg); }
	&__wrap ul li:nth-child(6)::before {
		background: url(../img/step_06.svg); }
	&__wrap ul li p {
		font-size: 12px;
		color: #393e6b; }
	&__wrap ul li span {
		font-weight: 700; }
	&__wrap div {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 80px; }
	&__wrap div a {
		background: #2cd3b8;
		border-radius: 50px;
		padding-top: 10px;
		padding-bottom: 10px;
		text-align: center;
		text-decoration: none;
		text-transform: uppercase;
		font-size: 12px;
		font-weight: 700;
		color: #fff; }
	&__wrap div a:hover {
		background: #393e6b; } }

@media (min-width: 900px) {
	.step {
		&__wrap {
			padding-top: 40px;
			padding-bottom: 60px; }
		&__wrap h3 {
			font-size: 30px; }
		&__wrap p {
			font-size: 16px; }
		&__wrap ul {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			height: 550px;
			margin-top: 60px;
			width: 70%; }
		&__wrap ul li {
			padding-left: 110px; }
		&__wrap ul li::before {
			top: calc(50% - 40px);
			left: 0;
			width: 80px;
			height: 80px; }
		&__wrap ul li p {
			font-size: 18px; }
		&__wrap ul li span {
			font-size: 24px; }
		&__wrap div {
			display: flex;
			flex-direction: row;
			justify-content: flex-start;
			height: auto;
			margin-top: 80px; }
		&__wrap div a {
			box-sizing: border-box;
			font-size: 14px;
			margin-right: 20px;
			padding: 15px 0;
			width: 250px; } } }
