.footer {
	background: #393f6b;
	&__wrap {
		padding-top: 20px;
		padding-bottom: 20px; }
	&__wrap p {
		font-size: 12px;
		color: #d8d9e5; }
	&__wrap div {
		display: none; } }

@media (min-width: 900px) {
	.footer {
		&__wrap {
			padding-top: 20px;
			padding-bottom: 20px;
			display: flex;
			justify-content: space-between; }
		&__wrap div {
			display: block; }
		&__wrap > div {
			display: flex;
			flex-direction: column; }
		&__wrap div a {
			color: #fff;
			margin-top: 10px; }
		&__wrap .tel {
			padding-left: 30px;
			position: relative;
			margin-bottom: 10px; }
		&__wrap .tel::before {
			content: '';
			position: absolute;
			top: calc(50% - 10px);
			left: 0;
			width: 20px;
			height: 20px;
			border-radius: 50%;
			background: green; }
		& i {
			font-size: 20px;
			color: #fff; } } }
