.setting {
	background-color: #eff4f4;
	&__wrap {
		padding-top: 20px;
		padding-bottom: 40px; }
	&__wrap h3 {
		font-size: 28px;
		color: #393e6b;
		text-transform: uppercase; }
	&__wrap p {
		width: 85%;
		font-size: 12px;
		color: #74757b;
		margin-bottom: 40px; }
	&__wrap div {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 80px; }
	&__wrap div a {
		background-color: #2cd3b8;
		border: 1px solid #2cd3b8;
		border-radius: 50px;
		padding-top: 10px;
		padding-bottom: 10px;
		text-align: center;
		text-decoration: none;
		text-transform: uppercase;
		font-size: 12px;
		font-weight: 700;
		color: #fff; }
	&__wrap div a:hover {
		background-color: #fff;
		border-color: #393e6b;
		color: #393e6b; } }

@media (min-width: 900px) {
	.setting {
		padding-top: 40px;
		padding-bottom: 60px;
		&__wrap h3 {
			font-size: 36px; }
		&__wrap p {
			width: 60%;
			font-size: 16px;
			color: #74757b;
			margin-bottom: 40px; }
		&__wrap div {
			display: flex;
			flex-direction: row;
			justify-content: flex-start;
			height: auto; }
		&__wrap div a {
			box-sizing: border-box;
			background-color: #2cd3b8;
			border: 1px solid #2cd3b8;
			border-radius: 50px;
			padding: 10px 0;
			margin-right: 20px;
			width: 250px;
			font-size: 16px; }
		&__wrap div a:hover {
			background-color: #fff;
			border-color: #393e6b;
			color: #393e6b; } } }
