.advantages {
	&__wrap {
		padding-top: 20px;
		padding-bottom: 40px; }
	&__wrap h3 {
		font-size: 28px;
		color: #393e6b;
		text-transform: uppercase; }
	&__wrap p {
		width: 85%;
		font-size: 12px;
		color: #74757b;
		margin-bottom: 40px; }
	&__step {
		display: flex; }
	&__step div {
		display: flex;
		flex-direction: column;
		margin-right: 30px; }
	&__step div span {
		position: relative;
		font-size: 60px;
		font-weight: 700;
		color: #e8e8ec; }
	&__step div span::before {
		content: '';
		position: absolute;
		top: 20px;
		left: 20px;
		width: 30px;
		height: 30px;
		background: url(../img/01.svg) no-repeat; }
	&__step div:last-child span::before {
		background: url(../img/02.svg) no-repeat; }
	&__step div a {
		font-size: 12px;
		font-weight: 600;
		color: #2cd3b8;
		margin-left: 10px;
		width: 80px; }
	&__step div a:hover {
		color: #393e6b; } }

@media (min-width: 900px) {
	.advantages {
		padding-top: 40px;
		padding-bottom: 60px;
		&__wrap h3 {
			font-size: 30px; }
		&__wrap p {
			font-size: 16px; }
		&__step div {
			margin-right: 80px; }
		&__step div span {
			font-size: 150px; }
		&__step div span::before {
			top: 80px;
			left: 50px;
			width: 45px;
			height: 45px; }
		&__step div a {
			font-size: 16px;
			width: auto; } } }
